/* src/styles/Lessons.css */

/* ========== AI Info Banner ========== */
.ai-info-banner {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 15px;
  padding: 2rem;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  color: white;
  box-shadow: 0 10px 30px rgba(102, 126, 234, 0.3);
}

.banner-icon {
  font-size: 4rem;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.banner-content h3 {
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
}

.banner-content p {
  opacity: 0.9;
  line-height: 1.6;
}


.lesson-card.adaptive-card {
  border: 3px solid #667eea;
  position: relative;
  background: white;
  box-shadow: 0 8px 30px rgba(102, 126, 234, 0.3);
}


/* ========== Adaptive Lesson Cards ========== */
/*.lesson-card.adaptive-card {
  border: 3px solid #667eea;
  position: relative;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%);
  box-shadow: 0 8px 30px rgba(102, 126, 234, 0.3);
}*/

.lesson-card.adaptive-card:hover {
  transform: translateY(-15px);
  box-shadow: 0 20px 60px rgba(102, 126, 234, 0.5);
  border-color: #764ba2;
}

.ai-badge-corner {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0.5rem 1.2rem;
  border-radius: 25px;
  font-size: 0.85rem;
  font-weight: bold;
  letter-spacing: 1.5px;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.5);
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { 
    transform: scale(1);
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.5);
  }
  50% { 
    transform: scale(1.05);
    box-shadow: 0 6px 25px rgba(102, 126, 234, 0.8);
  }
}

.lesson-card.adaptive-card .lesson-icon {
  font-size: 5rem;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.lesson-card.adaptive-card .lesson-title {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 800;
}

.lesson-card.adaptive-card .lesson-description {
  color: #555;
  font-weight: 500;
  line-height: 1.6;
}

.lesson-difficulty.adaptive {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0.5rem 1.2rem;
  border-radius: 25px;
  font-weight: 700;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.start-lesson-button.adaptive-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  position: relative;
  overflow: hidden;
  font-weight: 700;
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
  border: none;
  padding: 1.2rem 2rem;
}

.start-lesson-button.adaptive-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.start-lesson-button.adaptive-button:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 35px rgba(102, 126, 234, 0.6);
}

/* ========== Breathing Animation ========== */
.breathing-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  padding: 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  color: white;
}

.breathing-title {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  text-align: center;
}

.breathing-subtitle {
  font-size: 1.2rem;
  margin-bottom: 3rem;
  opacity: 0.9;
}

.breathing-circle-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem 0;
  height: 300px;
}

.breathing-circle {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(10px);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
}

.breathing-text {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
}

.breathing-info {
  margin-top: 2rem;
  text-align: center;
}

.breathing-info p {
  margin: 0.5rem 0;
  font-size: 1.1rem;
}

.skip-button {
  margin-top: 2rem;
  padding: 0.8rem 2rem;
  background: white;
  color: #667eea;
  border: none;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.skip-button:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}

/* ========== Number Card ========== */
.number-card {
  background: white;
  border-radius: 20px;
  padding: 3rem;
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.3s;
}

.number-card:hover {
  transform: scale(1.05);
}

.number-display {
  font-size: 6rem;
  font-weight: bold;
  color: #667eea;
  margin-bottom: 1rem;
}

.emoji-group {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
  margin: 2rem 0;
}

.emoji-item {
  font-size: 3rem;
  animation: popIn 0.5s ease backwards;
}

.emoji-item:nth-child(1) { animation-delay: 0.1s; }
.emoji-item:nth-child(2) { animation-delay: 0.2s; }
.emoji-item:nth-child(3) { animation-delay: 0.3s; }
.emoji-item:nth-child(4) { animation-delay: 0.4s; }
.emoji-item:nth-child(5) { animation-delay: 0.5s; }

@keyframes popIn {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.number-text {
  font-size: 1.5rem;
  color: #333;
  margin: 1.5rem 0;
  font-weight: 600;
}

.speak-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 50px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s;
}

.speak-button:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
}

/* ========== Drag & Drop Game ========== */
.drag-drop-game {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

.game-instruction {
  font-size: 1.5rem;
  color: #333;
  text-align: center;
  margin-bottom: 2rem;
}

.items-source {
  margin-bottom: 2rem;
}

.source-label {
  font-weight: 600;
  color: #666;
  margin-bottom: 1rem;
}

.items-container {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  padding: 1.5rem;
  background: #f8f9fa;
  border-radius: 15px;
  border: 2px dashed #ccc;
}

.draggable-item {
  width: 80px;
  height: 80px;
  background: white;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: grab;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s;
}

.draggable-item:active {
  cursor: grabbing;
  opacity: 0.5;
}

.draggable-item:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
}

.item-emoji {
  font-size: 3rem;
}

.drop-zone {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  padding: 2rem;
  min-height: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px dashed white;
}

.drop-zone-label {
  text-align: center;
  color: white;
  margin-bottom: 1rem;
}

.target-number {
  font-size: 4rem;
  font-weight: bold;
  display: block;
}

.dropped-items {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1rem 0;
  min-height: 100px;
}

.dropped-item {
  width: 80px;
  height: 80px;
  background: white;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
  animation: dropIn 0.3s ease;
}

.dropped-item:hover .remove-hint {
  opacity: 1;
}

.remove-hint {
  position: absolute;
  top: -10px;
  right: -10px;
  background: #ef4444;
  color: white;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  opacity: 0;
  transition: opacity 0.3s;
}

@keyframes dropIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.8);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.drop-count {
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
  margin-top: 1rem;
}

.feedback-message {
  margin-top: 2rem;
  padding: 1.5rem;
  border-radius: 15px;
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
  animation: fadeIn 0.5s ease;
}

.feedback-message.success {
  background: #d1fae5;
  color: #065f46;
  border: 2px solid #10b981;
}

.feedback-message.info {
  background: #fef3c7;
  color: #92400e;
  border: 2px solid #f59e0b;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* ========== Lesson Hub ========== */
.lesson-hub-page {
  min-height: calc(100vh - 100px);
}

.lesson-hub-container {
  max-width: 1200px;
  margin: 0 auto;
}

.hub-header {
  text-align: center;
  margin-bottom: 3rem;
  background: white;
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.hub-header h1 {
  font-size: 2.5rem;
  color: #667eea;
  margin-bottom: 1rem;
}

.hub-header p {
  font-size: 1.2rem;
  color: #666;
}

.lessons-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

/* Regular lesson card */
.lesson-card {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  border: 2px solid transparent;
}

.lesson-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.15);
}

.lesson-icon {
  font-size: 4rem;
  text-align: center;
  margin-bottom: 1rem;
}

.lesson-title {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.lesson-description {
  color: #666;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.lesson-meta {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

.lesson-difficulty {
  background: #e3f2fd;
  color: #1976d2;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
}

.lesson-duration {
  color: #666;
  font-size: 0.9rem;
}

.completion-badge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: #d1fae5;
  color: #065f46;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
}

.start-lesson-button {
  width: 100%;
  padding: 1rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.start-lesson-button:hover {
  transform: scale(1.02);
  box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
}

.hub-footer {
  text-align: center;
  padding: 2rem;
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.hub-footer p {
  color: #666;
  font-size: 1.1rem;
}

/* ========== Counting Lesson Page ========== */
.counting-lesson-page {
  max-width: 1000px;
  margin: 0 auto;
}

.lesson-progress-bar {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.progress-steps {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.progress-steps::before {
  content: '';
  position: absolute;
  top: 25px;
  left: 10%;
  right: 10%;
  height: 3px;
  background: #e0e0e0;
  z-index: 0;
}

.progress-step {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}

.step-circle {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #e0e0e0;
  color: #999;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 1.2rem;
  transition: all 0.3s;
}

.progress-step.active .step-circle {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.progress-step.completed .step-circle {
  background: #10b981;
  color: white;
}

.step-label {
  margin-top: 0.5rem;
  font-size: 0.85rem;
  color: #666;
  font-weight: 600;
}

/* Section Layouts */
.visual-learning-section,
.count-repeat-section,
.number-practice-section {
  background: white;
  border-radius: 20px;
  padding: 3rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.section-title {
  font-size: 2rem;
  color: #333;
  text-align: center;
  margin-bottom: 0.5rem;
}

.section-subtitle {
  font-size: 1.1rem;
  color: #666;
  text-align: center;
  margin-bottom: 2rem;
}

.progress-indicator {
  text-align: center;
  font-weight: 600;
  color: #667eea;
  margin-bottom: 2rem;
}

.lesson-navigation {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
  gap: 1rem;
}

/* Count & Repeat Section */
.numbers-display {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 3rem 0;
  flex-wrap: wrap;
}

.number-bubble {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.bubble-number {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  font-weight: bold;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.bubble-emoji {
  font-size: 2rem;
}

.speak-together-button {
  display: block;
  margin: 2rem auto;
  padding: 1.5rem 3rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 1.3rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.speak-together-button:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

.repeat-count {
  text-align: center;
  font-size: 1.1rem;
  color: #666;
  margin-top: 1rem;
}

/* Reward Section */
.reward-section {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  padding: 3rem;
  color: white;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.floating-stars {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
}

.floating-star {
  position: absolute;
  font-size: 2rem;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.completion-badge {
  margin: 2rem auto;
}

.badge-circle {
  width: 150px;
  height: 150px;
  background: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 1rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.badge-emoji {
  font-size: 5rem;
}

.badge-text {
  font-size: 1.5rem;
  font-weight: bold;
}

.lesson-summary {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 2rem;
  margin: 2rem auto;
  max-width: 500px;
  backdrop-filter: blur(10px);
}

.lesson-summary h3 {
  margin-bottom: 1rem;
}

.lesson-summary ul {
  list-style: none;
  padding: 0;
}

.lesson-summary li {
  padding: 0.5rem 0;
  font-size: 1.1rem;
}

.reward-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 2rem;
  flex-wrap: wrap;
}

.action-button.calm {
  background: rgba(255, 255, 255, 0.3);
  border: 2px solid white;
}

.action-button.calm:hover {
  background: white;
  color: #667eea;
}

/* ========== Certificate Page ========== */
.lesson-complete-page {
  min-height: calc(100vh - 100px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.certificate-container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

.certificate {
  background: white;
  padding: 3rem;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
  margin-bottom: 2rem;
}

.certificate-border {
  border: 5px solid #667eea;
  border-radius: 15px;
  padding: 2rem;
}

.certificate-content {
  text-align: center;
}

.certificate-title {
  font-size: 2rem;
  color: #667eea;
  margin-bottom: 2rem;
}

.certificate-text {
  color: #666;
  font-size: 1.1rem;
  margin: 1rem 0;
}

.student-name {
  font-size: 2.5rem;
  color: #333;
  margin: 1rem 0;
  font-family: 'Brush Script MT', cursive;
}

.lesson-name {
  font-size: 1.8rem;
  color: #667eea;
  margin: 1rem 0;
}

.certificate-details {
  margin: 2rem 0;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 10px;
}

.certificate-badges {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 2rem 0;
}

.badge-item {
  text-align: center;
}

.badge-icon {
  font-size: 3rem;
  display: block;
  margin-bottom: 0.5rem;
}

.badge-item p {
  font-size: 0.9rem;
  color: #666;
  font-weight: 600;
}

.certificate-footer {
  margin-top: 3rem;
}

.signature-line {
  border-top: 2px solid #333;
  width: 250px;
  margin: 0 auto;
  padding-top: 0.5rem;
}

.signature-label {
  color: #666;
  font-size: 0.9rem;
}

.completion-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.action-button.download {
  background: #10b981;
}

.action-button.download:hover {
  background: #059669;
}

/* ========== Reward Animation ========== */
.reward-container {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  padding: 3rem;
  min-height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.reward-content {
  text-align: center;
  color: white;
  z-index: 2;
}

.reward-title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.reward-message {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.confetti-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
}

.confetti-particle {
  position: absolute;
  font-size: 2rem;
  animation: confettiFall 3s ease-in infinite;
}

@keyframes confettiFall {
  0% {
    top: -10%;
    opacity: 1;
    transform: rotate(0deg);
  }
  100% {
    top: 110%;
    opacity: 0;
    transform: rotate(360deg);
  }
}

/* ========== Responsive Design ========== */
@media (max-width: 768px) {
  .breathing-title {
    font-size: 1.5rem;
  }

  .number-display {
    font-size: 4rem;
  }

  .emoji-item {
    font-size: 2rem;
  }

  .lessons-grid {
    grid-template-columns: 1fr;
  }

  .progress-steps {
    flex-wrap: wrap;
    gap: 1rem;
  }

  .numbers-display {
    gap: 1rem;
  }

  .bubble-number {
    width: 60px;
    height: 60px;
    font-size: 2rem;
  }

  .certificate {
    padding: 1.5rem;
  }

  .certificate-badges {
    flex-direction: column;
    gap: 1rem;
  }

  .completion-actions {
    flex-direction: column;
  }

  .action-button {
    width: 100%;
  }
}