/* src/styles/Dashboard.css */

/* ========== Dashboard Page Styles ========== */
.dashboard-page {
  min-height: calc(100vh - 100px);
  padding: 20px;
}

.dashboard-container {
  max-width: 1200px;
  margin: 0 auto;
}

/* Dashboard Header */
.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.dashboard-header h1 {
  color: #667eea;
  font-size: 2rem;
}

.take-quiz-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 0.8rem 2rem;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.take-quiz-button:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

/* Empty State */
.empty-state {
  background: white;
  border-radius: 20px;
  padding: 4rem 2rem;
  text-align: center;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.empty-icon {
  font-size: 5rem;
  display: block;
  margin-bottom: 1rem;
}

.empty-state h2 {
  color: #333;
  margin-bottom: 1rem;
}

.empty-state p {
  color: #666;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

/* Results Section */
.results-section {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.results-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #f0f0f0;
}

.results-header h2 {
  color: #333;
  font-size: 1.5rem;
}

.clear-button {
  background: #ef4444;
  color: white;
  border: none;
  padding: 0.6rem 1.5rem;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.clear-button:hover {
  background: #dc2626;
  transform: scale(1.05);
}

/* Results Table */
.results-table-container {
  overflow-x: auto;
}

.results-table {
  width: 100%;
  border-collapse: collapse;
}

.results-table thead {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.results-table th {
  padding: 1rem;
  text-align: left;
  font-weight: 600;
  font-size: 0.95rem;
}

.results-table th:first-child {
  border-radius: 10px 0 0 0;
}

.results-table th:last-child {
  border-radius: 0 10px 0 0;
}

.results-table tbody tr {
  border-bottom: 1px solid #f0f0f0;
  transition: background 0.3s;
}

.results-table tbody tr:hover {
  background: #f8f9fa;
}

.results-table tbody tr:last-child {
  border-bottom: none;
}

.results-table td {
  padding: 1rem;
  color: #333;
}

/* Badges */
.percentage-badge {
  display: inline-block;
  background: #e3f2fd;
  color: #1976d2;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-weight: 600;
  font-size: 0.9rem;
}

.level-badge {
  display: inline-block;
  color: white;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-weight: 600;
  font-size: 0.85rem;
  text-align: center;
}

/* Delete Button */
.delete-button {
  background: transparent;
  color: #ef4444;
  border: 2px solid #ef4444;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.delete-button:hover {
  background: #ef4444;
  color: white;
}

/* ========== Responsive Design ========== */
@media (max-width: 768px) {
  .dashboard-header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .dashboard-header h1 {
    font-size: 1.5rem;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }

  .results-header {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }

  .results-table-container {
    overflow-x: scroll;
  }

  .results-table {
    min-width: 600px;
  }

  .results-table th,
  .results-table td {
    padding: 0.8rem 0.5rem;
    font-size: 0.85rem;
  }
}

@media (max-width: 480px) {
  .dashboard-page {
    padding: 10px;
  }

  .dashboard-header {
    padding: 1.5rem;
  }

  .results-section {
    padding: 1.5rem;
  }

  .empty-state {
    padding: 3rem 1.5rem;
  }

  .empty-icon {
    font-size: 4rem;
  }
}