/* src/styles/MLComponents.css */

/* ========== Adaptive Exercise ========== */
.adaptive-exercise {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 0 auto;
}

.exercise-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.difficulty-badge {
  padding: 0.5rem 1.5rem;
  border-radius: 50px;
  font-weight: 600;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.difficulty-badge[data-level="1"] {
  background: #d1fae5;
  color: #065f46;
}

.difficulty-badge[data-level="2"] {
  background: #dbeafe;
  color: #1e40af;
}

.difficulty-badge[data-level="3"] {
  background: #fef3c7;
  color: #92400e;
}

.difficulty-badge[data-level="4"] {
  background: #fed7aa;
  color: #9a3412;
}

.difficulty-badge[data-level="5"] {
  background: #fecaca;
  color: #991b1b;
}

.progress-info {
  font-weight: 600;
  color: #667eea;
}

.exercise-content {
  margin-bottom: 2rem;
}

.visual-display {
  text-align: center;
  margin: 2rem 0;
  padding: 2rem;
  background: #f8f9fa;
  border-radius: 15px;
}

.visual-emojis {
  font-size: 3rem;
  line-height: 1.5;
}

.exercise-question {
  font-size: 1.8rem;
  color: #333;
  text-align: center;
  margin: 2rem 0;
}

.answer-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.answer-display {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  padding: 1.5rem;
  border-radius: 15px;
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.number-pad {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  max-width: 300px;
  margin: 0 auto;
}

.num-button, .clear-button, .backspace-button {
  padding: 1.5rem;
  font-size: 1.5rem;
  font-weight: 600;
  border: 2px solid #e0e0e0;
  background: white;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.2s;
}

.num-button:hover, .clear-button:hover, .backspace-button:hover {
  background: #667eea;
  color: white;
  border-color: #667eea;
  transform: scale(1.05);
}

.num-button:active, .clear-button:active, .backspace-button:active {
  transform: scale(0.95);
}

.clear-button {
  grid-column: span 2;
  background: #fecaca;
  border-color: #ef4444;
  color: #991b1b;
}

.clear-button:hover {
  background: #ef4444;
  color: white;
}

.backspace-button {
  background: #fef3c7;
  border-color: #f59e0b;
  color: #92400e;
}

.backspace-button:hover {
  background: #f59e0b;
  color: white;
}

.submit-button {
  padding: 1.2rem 2rem;
  font-size: 1.2rem;
  font-weight: 600;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s;
}

.submit-button:hover:not(:disabled) {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
}

.submit-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.feedback {
  padding: 1.5rem;
  border-radius: 15px;
  text-align: center;
  font-size: 1.3rem;
  font-weight: 600;
  animation: slideIn 0.3s ease;
}

.feedback.correct {
  background: #d1fae5;
  color: #065f46;
  border: 2px solid #10b981;
}

.feedback.incorrect {
  background: #fee2e2;
  color: #991b1b;
  border: 2px solid #ef4444;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.session-stats {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 2rem;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-weight: 600;
  font-size: 1.2rem;
}

.stat-item.correct {
  background: #d1fae5;
  color: #065f46;
}

.stat-item.incorrect {
  background: #fee2e2;
  color: #991b1b;
}

/* ========== ML Analytics ========== */
.ml-analytics {
  background: white;
  border-radius: 20px;
  padding: 2.5rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1);
}

.analytics-title {
  font-size: 2rem;
  color: #333;
  margin-bottom: 2rem;
  text-align: center;
}

.analytics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.analytics-card {
  background: #f8f9fa;
  border-radius: 15px;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: transform 0.3s;
}

.analytics-card:hover {
  transform: translateY(-5px);
}

.card-icon {
  font-size: 3rem;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
}

.card-content h3 {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.card-value {
  font-size: 2rem;
  font-weight: bold;
  color: #333;
  margin: 0;
}

.card-subtitle {
  font-size: 0.85rem;
  color: #999;
  margin-top: 0.3rem;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: #e0e0e0;
  border-radius: 10px;
  overflow: hidden;
  margin-top: 0.8rem;
}

.progress-fill {
  height: 100%;
  border-radius: 10px;
  transition: width 0.5s ease;
}

.progress-fill.success {
  background: linear-gradient(90deg, #10b981 0%, #059669 100%);
}

/* Prediction Section */
.prediction-section {
  background: #f8f9fa;
  border-radius: 15px;
  padding: 2rem;
  margin-bottom: 2rem;
}

.prediction-section h3 {
  color: #333;
  margin-bottom: 1.5rem;
}

.prediction-card {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
}

.prediction-visual {
  margin-bottom: 1rem;
}

.prediction-meter {
  width: 100%;
  height: 40px;
  background: #e0e0e0;
  border-radius: 20px;
  overflow: hidden;
  margin-bottom: 1rem;
}

.prediction-fill {
  height: 100%;
  border-radius: 20px;
  transition: width 0.8s ease;
}

.prediction-label {
  font-size: 1.1rem;
  color: #333;
  text-align: center;
}

.prediction-details {
  display: flex;
  justify-content: space-around;
  padding-top: 1rem;
  border-top: 2px solid #f0f0f0;
}

.prediction-details p {
  color: #666;
  font-size: 0.95rem;
}

/* Insights Grid */
.insights-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.insight-card {
  border-radius: 15px;
  padding: 1.5rem;
}

.insight-card.struggling {
  background: #fef3c7;
  border-left: 4px solid #f59e0b;
}

.insight-card.strengths {
  background: #d1fae5;
  border-left: 4px solid #10b981;
}

.insight-card h3 {
  margin-bottom: 1rem;
  color: #333;
}

.insight-card ul {
  list-style: none;
  padding: 0;
}

.insight-card li {
  padding: 0.5rem 0;
  color: #666;
  font-weight: 500;
}

.insight-card li::before {
  content: " ";
  color: inherit;
  font-weight: bold;
  margin-right: 0.5rem;
}

.insight-note {
  margin-top: 1rem;
  font-size: 0.85rem;
  color: #999;
  font-style: italic;
}

/* Recommendations */
.recommendations-section {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 15px;
  padding: 2rem;
  color: white;
  margin-bottom: 2rem;
}

.recommendations-section h3 {
  margin-bottom: 1.5rem;
}

.recommendations-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.recommendation-item {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  padding: 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  backdrop-filter: blur(10px);
  border-left: 4px solid white;
}

.recommendation-item.priority-high {
  border-left-color: #ef4444;
  background: rgba(255, 255, 255, 0.3);
}

.recommendation-item.priority-medium {
  border-left-color: #f59e0b;
}

.rec-icon {
  font-size: 2rem;
  width: 50px;
  text-align: center;
}

.rec-content {
  flex: 1;
}

.rec-message {
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
}

.rec-type {
  font-size: 0.75rem;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Performance Chart */
.chart-section {
  background: #f8f9fa;
  border-radius: 15px;
  padding: 2rem;
}

.chart-section h3 {
  color: #333;
  margin-bottom: 1.5rem;
}

.chart-container {
  display: flex;
  align-items: flex-end;
  justify-content: space-around;
  height: 200px;
  padding: 1rem;
  background: white;
  border-radius: 12px;
  gap: 0.5rem;
}

.chart-bar {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.bar-fill {
  width: 100%;
  max-width: 50px;
  background: linear-gradient(180deg, #667eea 0%, #764ba2 100%);
  border-radius: 8px 8px 0 0;
  transition: height 0.5s ease;
  min-height: 10px;
}

.bar-label {
  margin-top: 0.5rem;
  font-size: 0.75rem;
  color: #666;
}

.chart-caption {
  text-align: center;
  margin-top: 1rem;
  color: #666;
  font-size: 0.9rem;
}

/* ========== Adaptive Lesson Page ========== */
.adaptive-lesson-page {
  min-height: calc(100vh - 100px);
  padding: 2rem;
}

.lesson-container {
  max-width: 1000px;
  margin: 0 auto;
}

.lesson-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.lesson-header h1 {
  color: #333;
  font-size: 2rem;
}

.back-button, .analytics-button {
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.back-button {
  background: #e0e0e0;
  color: #333;
}

.back-button:hover {
  background: #d0d0d0;
  transform: translateX(-5px);
}

.analytics-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.analytics-button:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
}

.ai-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0.8rem 1.5rem;
  border-radius: 50px;
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.badge-icon {
  font-size: 1.5rem;
}

.info-banner {
  background: #e3f2fd;
  border-left: 4px solid #1976d2;
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 2rem;
}

.info-banner p {
  color: #1565c0;
  line-height: 1.6;
}

/* Results Container */
.results-container {
  background: white;
  border-radius: 20px;
  padding: 3rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  max-width: 700px;
  margin: 0 auto;
}

.results-header {
  text-align: center;
  margin-bottom: 2rem;
}

.results-header h1 {
  color: #333;
  margin-bottom: 2rem;
}

.score-circle {
  width: 180px;
  height: 180px;
  border: 8px solid #667eea;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 10px 30px rgba(102, 126, 234, 0.3);
}

.score-value {
  font-size: 3.5rem;
  font-weight: bold;
}

.score-label {
  font-size: 1rem;
  opacity: 0.9;
}

.results-stats {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 2rem 0;
  flex-wrap: wrap;
}

.stat-box {
  background: #f8f9fa;
  padding: 1.5rem;
  border-radius: 15px;
  text-align: center;
  min-width: 120px;
}

.stat-box.correct {
  border-top: 4px solid #10b981;
}

.stat-box.incorrect {
  border-top: 4px solid #ef4444;
}

.stat-box.total {
  border-top: 4px solid #3b82f6;
}

.stat-icon {
  font-size: 2rem;
  display: block;
  margin-bottom: 0.5rem;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
  display: block;
}

.stat-label {
  font-size: 0.9rem;
  color: #666;
  display: block;
  margin-top: 0.3rem;
}

.results-message {
  margin: 2rem 0;
}

.message {
  padding: 1.5rem;
  border-radius: 15px;
  text-align: center;
}

.message.success {
  background: #d1fae5;
  border: 2px solid #10b981;
}

.message.good {
  background: #dbeafe;
  border: 2px solid #3b82f6;
}

.message.encourage {
  background: #fef3c7;
  border: 2px solid #f59e0b;
}

.message h3 {
  margin-bottom: 0.5rem;
  color: #333;
}

.message p {
  color: #666;
  line-height: 1.6;
}

.results-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 2rem;
  flex-wrap: wrap;
}

.action-btn {
  padding: 1rem 2rem;
  border: none;
  border-radius: 50px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.action-btn.primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.action-btn.primary:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(102, 126, 234, 0.4);
}

.action-btn.secondary {
  background: white;
  color: #667eea;
  border: 2px solid #667eea;
}

.action-btn.secondary:hover {
  background: #667eea;
  color: white;
}

.action-btn.tertiary {
  background: #e0e0e0;
  color: #333;
}

.action-btn.tertiary:hover {
  background: #d0d0d0;
}

/* ========== Responsive Design ========== */
@media (max-width: 768px) {
  .adaptive-exercise {
    padding: 1.5rem;
  }

  .visual-emojis {
    font-size: 2rem;
  }

  .exercise-question {
    font-size: 1.4rem;
  }

  .answer-display {
    font-size: 2rem;
    padding: 1rem;
  }

  .number-pad {
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5rem;
  }

  .num-button, .clear-button, .backspace-button {
    padding: 1rem;
    font-size: 1.2rem;
  }

  .analytics-grid {
    grid-template-columns: 1fr;
  }

  .insights-grid {
    grid-template-columns: 1fr;
  }

  .results-stats {
    flex-direction: column;
    align-items: stretch;
  }

  .results-actions {
    flex-direction: column;
  }

  .action-btn {
    width: 100%;
  }

  .lesson-header {
    flex-direction: column;
    align-items: stretch;
  }

  .lesson-header h1 {
    font-size: 1.5rem;
    text-align: center;
  }
}